.main-nav {
  display: flex;
  position: absolute;
  z-index: 3;
}

.main-nav__toggle {
  width: 23px;
  height: 22px;
  display: block;
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 17px;
  z-index: 2;
  border: none;
  background-color: #81b3d2;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  background-color: #ffffff;
  width: 23px;
  height: 4px;
  top: -2px;
  left: 0;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}

/*
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: $blue;
  box-shadow: 0 8px 0 0 $blue, 0 16px 0 0 $blue;
  opacity: 0.6;
}
*/
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  background-color: #ffffff;
  width: 30px;
  height: 2px;
  top: 10px;
  left: -5px;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before, .main-nav--opened .main-nav__toggle:hover::after {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.3;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #81b3d2;
  display: block;
  text-align: center;
  width: 320px;
}

.main-nav__nojs .main-nav__toggle {
  display: none;
}

.main-nav__nojs .main-nav__wrapper {
  position: static;
  min-height: none;
}

.main-nav__nojs .main-nav__list {
  display: block;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }

  .main-nav__item {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    border-bottom: 1px solid rgba(1, 1, 1, 0.2);
    padding: 17px 70px 12px 70px;
  }

  .main-nav__item--shadow {
    border-bottom: 0;
  }

  .main-nav__item--shadow::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    height: 7px;
    width: 100%;
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }

  /* .main-nav--opened .main-nav__item {
    display: none;
  }

  .main-nav--opened .main-nav__toggle {
    display: none;
  }
  */
}
