@use "../base/variables.scss" as *;

.hero {
  position: relative;
}

.hero__wrapper {
  background-color: #6999c1;
  background-image: url("../img/index-mobile/hero-index.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 389px;
  padding-top: 90px;
  padding-bottom: 85px;
}

.hero__wrapper::before {
  content: "";
  position: absolute;
  background-image: url("../img/index-mobile/vector-left.svg");
  background-repeat: no-repeat;
  background-position: left;
  width: 160px;
  height: 57px;
  z-index: 4;
  bottom: 0;
  left: 0;
}

.hero__wrapper::after {
  content: "";
  position: absolute;
  background-image: url("../img/index-mobile/vector-right.svg");
  background-repeat: no-repeat;
  background-position: right;
  width: 160px;
  height: 57px;
  z-index: 4;
  bottom: 0;
  right: 0;
}

.hero__image {
  display: block;
  margin: 0 auto;
}

@media (min-width: $tablet-width) {
  .hero__wrapper {
    min-height: 518px;
    background-image: url("../img/index-tablet/hero-index-tablet.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding-top: 77px;
    padding-bottom: 91px;
  }
}

@media (min-width: $tablet-width) {
  .hero__wrapper {
    padding-top: 78px;
    padding-bottom: 90px;
  }
}
